.s4 {
  padding: 90px 0;
  overflow: visible;
   background-color: #ffffff;

  h2 {
    margin-bottom: 95px;
  }

  .container {
    padding: 0 96px;
  }

  .build-type-container {
    position: relative;
  }

  .build-type {
    position: relative;
    width: 100%;
    height: 180px;
    overflow: hidden;
  }

  .prev-slide,
  .next-slide {
    position: absolute;
    top: 50%;
    transform: translateY(-100%);
    z-index: 100;
    cursor: pointer;
  }

  .prev-slide {
    right: 101%;
  }

  .next-slide {
    left: 101%;

    svg {
      transform: rotate(180deg);
    }
  }

  .swiper-slide {
    display: flex;
    flex-direction: column;
  }

  // .swiper-slide-next {
  //   transition: color .15s linear;
  //   &.active-color {

  //     .slide-text {
  //       color: #F3914E;
  //     }
  //   }
  // }

  .swiper-arrow {
    fill: #525252;
    width: 16px;
    height: 32px;
    transition: all .15s linear;

    &:hover {
     fill: #F3914E;
    }

    // path {
    //   stroke-width: 3px;
    // }
  }

  .slide-content {
    cursor: pointer;
    transition: color .1s linear;
    &:hover {

      .slide-text {
        color: #F3914E;
      }
    }


    img {
      display: block;
      margin: 0 auto;
    }
  }

  .slide-img-wrap {
    height: 125px;
    display: flex;
    align-items: flex-end;
        margin-bottom: 8px;
  }

  .slide-text {
    display: block;
    text-align: center;
    margin: 0 auto;
    @extend %sdReg;
    font-size: 17px;
    letter-spacing: 0.17px;
    color: #525252;
    transition: color .1s linear;
  }

  .slide-text__bold {
    @extend %stsBold;
  }
}


